import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Button from '@/components/ui/Button';
import Input from '@/components/ui/Input';
import Card, { CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/Card';
import { useAuthStore } from '@/stores/authStore';
import { generateId } from '@/lib/utils';

interface RegisterForm {
  username: string;
  email: string;
  password: string;
  confirmPassword: string;
  displayName: string;
}

const RegisterPage: React.FC = () => {
  const navigate = useNavigate();
  const { login, isLoading } = useAuthStore();
  const [formData, setFormData] = useState<RegisterForm>({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    displayName: '',
  });
  const [errors, setErrors] = useState<Partial<RegisterForm>>({});

  const handleInputChange = (field: keyof RegisterForm, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // 
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }));
    }
  };

  const validateForm = (): boolean => {
    const newErrors: Partial<RegisterForm> = {};

    if (!formData.username.trim()) {
      newErrors.username = '';
    } else if (formData.username.length < 3) {
      newErrors.username = '3';
    }

    if (!formData.email.trim()) {
      newErrors.email = '';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = '';
    }

    if (!formData.displayName.trim()) {
      newErrors.displayName = '';
    }

    if (!formData.password) {
      newErrors.password = '';
    } else if (formData.password.length < 6) {
      newErrors.password = '6';
    }

    if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = '';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    // API
    const newUser = {
      id: generateId(),
      email: formData.email,
      username: formData.username,
      displayName: formData.displayName,
      createdAt: new Date(),
    };

    login(newUser);
    navigate('/lobby');
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-mystery-900">
      <Card className="max-w-md w-full mx-4" variant="elevated">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-mystery font-bold text-accent-500">
            
          </CardTitle>
          <CardDescription>
            
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <Input
              label=""
              placeholder=""
              value={formData.username}
              onChange={(e) => handleInputChange('username', e.target.value)}
              error={errors.username}
              isRequired
            />
            <Input
              label=""
              placeholder=""
              value={formData.displayName}
              onChange={(e) => handleInputChange('displayName', e.target.value)}
              error={errors.displayName}
              isRequired
            />
            <Input
              label=""
              type="email"
              placeholder=""
              value={formData.email}
              onChange={(e) => handleInputChange('email', e.target.value)}
              error={errors.email}
              isRequired
            />
            <Input
              label=""
              type="password"
              placeholder=""
              value={formData.password}
              onChange={(e) => handleInputChange('password', e.target.value)}
              error={errors.password}
              isRequired
            />
            <Input
              label=""
              type="password"
              placeholder=""
              value={formData.confirmPassword}
              onChange={(e) => handleInputChange('confirmPassword', e.target.value)}
              error={errors.confirmPassword}
              isRequired
            />
            <Button 
              type="submit" 
              className="w-full" 
              size="lg"
              isLoading={isLoading}
            >
              
            </Button>
            <Link to="/login">
              <Button variant="outline" className="w-full" size="lg">
                
              </Button>
            </Link>
          </form>
        </CardContent>
      </Card>
    </div>
  );
};

export default RegisterPage;
